import { useEffect } from "react";
import React from "react";
import { effect, signal } from "@preact/signals";
import { useLocation, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSignals } from "@preact/signals-react/runtime";

import { trans } from "@/i18n";
import { ThemeMode, RouterChange } from "@/utils/services/app.event";
import { AppRouter } from "@/utils/services/app.router";
import TypeButton from "@/types/type.button";
import TypeIcon, { IconName } from "@/types/type.icon";
import { SiteConfig } from "@/config/site-config";
import { SkeletonMenu } from "@/skeleton/skeleton-menu";

type RoleMenuAccessType = {
  id?: string;
  menuId?: string;
  name: string; // Default name (English)
  nameLang: Record<string, string>;
  href?: string;
  icon?: IconName;
  permissions?: {
    read: boolean;
    create: boolean;
    update: boolean;
    delete: boolean;
  };
  children?: RoleMenuAccessType[];
  priority?: number;
  type?: string;
  active?: boolean;
};

export const menuAccessIsLoading = signal<boolean>(false);
export const menuAccessData = signal<RoleMenuAccessType[]>([]);
export const menuAccessCall = async () => {
  try {
    menuAccessIsLoading.value = true;
    // const url = AppHttp.MsUrl.main + "/";
    await new Promise((resolve) => setTimeout(resolve, 1000));
    const resp = await Promise.resolve(SiteConfig);

    menuAccessData.value = resp;
  } finally {
    menuAccessIsLoading.value = false;
  }
};

export const Sidebar = ({
  isOpen = false,
  onToggle,
}: {
  isOpen: boolean;
  onToggle: () => void;
}) => {
  useSignals();
  const location = useLocation();

  useEffect(() => {
    menuAccessCall();
  }, []);

  function handleMenu(href: string | undefined): void {
    if (window.innerWidth < 1280) { // xl breakpoint
      onToggle();
    }
    if (!href) return;
    RouterChange(href, {});
  }

  return (
    <div
      className={`fixed inset-y-0 left-0 z-50 w-64 transform bg-base-100 transition-transform duration-300 ease-in-out xl:translate-x-0 xl:static xl:inset-0 ${isOpen ? "translate-x-0" : "-translate-x-full"
        } flex h-screen flex-col border-r border-base-300 shadow-xl xl:shadow-none`}
    >
      <SidebarHeader onToggle={onToggle} />

      <div className="flex-1 overflow-y-auto overflow-x-hidden p-4">
        {menuAccessIsLoading.value && <SkeletonMenu />}
        <ul className="menu menu-md w-full gap-2 p-0">
          {menuAccessData.value.map((item, index) => {
            const canReadParent = item.permissions?.read ?? true;
            if (!canReadParent) return null;

            const isParentActive = item.children
              ? item.children.some(
                (child) =>
                  location.pathname === child.href &&
                  (child.permissions?.read ?? true)
              ) || location.pathname === item.href
              : location.pathname === item.href;

            return (
              <li key={`${item.name}-${index}`}>
                {item.children ? (
                  <details open={isParentActive}>
                    <summary
                      className={`gap-3 ${isParentActive ? "bg-primary/10 text-primary font-medium" : ""
                        }`}
                    >
                      {item.icon && <TypeIcon name={item.icon} size={20} />}
                      <span className="flex-1">
                        {trans(item.nameLang, item.name)}
                      </span>
                    </summary>
                    <ul className="mt-2 flex flex-col gap-1 pl-4 before:opacity-20">
                      {item.children.map((child, childIndex) => {
                        const canReadChild = child.permissions?.read ?? true;
                        if (!canReadChild) return null;

                        const isChildActive = location.pathname === child.href;

                        return (
                          <li key={`${child.name}-${childIndex}`}>
                            <button
                              onClick={() => handleMenu(child.href)}
                              className={`gap-3 ${isChildActive
                                  ? "bg-primary text-primary-content font-bold shadow-md"
                                  : "hover:bg-base-200"
                                }`}
                            >
                              {child.icon && (
                                <TypeIcon name={child.icon} size={18} />
                              )}
                              <span>{trans(child.nameLang, child.name)}</span>
                            </button>
                          </li>
                        );
                      })}
                    </ul>
                  </details>
                ) : (
                  <button
                    onClick={() => handleMenu(item.href)}
                    className={`gap-3 ${isParentActive
                        ? "bg-primary text-primary-content font-bold shadow-md"
                        : "hover:bg-base-200"
                      }`}
                  >
                    {item.icon && <TypeIcon name={item.icon} size={20} />}
                    <span>{trans(item.nameLang, item.name)}</span>
                  </button>
                )}
                {index < menuAccessData.value.length - 1 && (
                  <div className="divider my-0 opacity-10"></div>
                )}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};

function SidebarHeader({ onToggle }: { onToggle: () => void }) {
  const { t } = useTranslation();

  return (
    <div className="sticky top-0 z-20 flex items-center justify-between border-b border-base-300 bg-base-100 p-4">
      <h2 className="text-xl font-bold tracking-tight text-base-content">
        {t("welcome")}
      </h2>
      <div className="flex items-center gap-2">
        <button
          onClick={() => RouterChange(AppRouter.LOGIN)}
          className="btn btn-ghost btn-circle btn-sm tooltip tooltip-bottom"
          data-tip="Logout"
        >
          <TypeIcon name="LogOut" size={18} />
        </button>
        <button
          onClick={onToggle}
          className="btn btn-ghost btn-circle btn-sm xl:hidden"
        >
          <TypeIcon name="X" size={20} />
        </button>
      </div>
    </div>
  );
}
